@media print {
  /* removes ucsd logo and other background images (glyphicons) */
  html, main, footer *,
  .layout-title *, {
    background-color: transparent !important;
    background-image: none !important;
    overflow: visible !important;
  }

  /* title and header font color should be black (readable) */
  .title-header,
  h1,h2,h3,h4,h5,h6,p
  {
    color: #000;
  }

  /* hide login/ emergency/ nav/ search/ footer links/ btn-nav styles */
  #uc-emergency, .layout-login, nav, .search, .footer-links, .btn-nav {
    display: none;
  }

  main {
    width: 99.99% !important;

    section {
      left: 0 !important;
      margin: 0 !important;
      padding: 0 !important;
      width: 100% !important;
    }
  }

  /* reset horizontal ruler */
  hr {
    background-color: #ccc !important;
  }

  .main-content-nav {
    background: none;
  }

  a[href]:after {
      content: none !important;
    }

}
