/* CUSTOMIZE THE CAROUSEL
-------------------------------------------------- */

.carousel {
  margin-bottom: 60px;

  .cr-item-container {
      position: absolute;
      top:20%;
      transition:all .2s linear;
      width: inherit;
         @media screen and (max-width: $xl-deskotp) {
           top:10%;
         }

         @media screen and (max-width: $desktop) {
           top: 5%;
         }

         @media screen and (max-width: $desktop-small) {
           top:20%;
           margin: 0 5%;
         }

         @media screen and (max-width: $tablet) {
            top:10%;
         }

         @media screen and (max-width: $mobile) {
           top:5%;
         }

       h1 {
          @media screen and (max-width: 1160px) {
            font-size: 40px;
          }
          @media screen and (max-width: $desktop-small) {
            font-size: 2em !important;
          }

          @media screen and (max-width: $mobile) {
            font-size: 1.2em !important;
          }
       }

       p {
           @media screen and (max-width: $desktop-small) {
             display: none;
           }
       }

       a.btn {

         @media screen and (max-width: $desktop-small) {
           padding: 8px;
           min-width: 150px;
           font-size: 13px;
         }

       }
  }

  .container {
    @media only screen and (max-width: 1320px) {
      width: 80%;
    }

    @media screen and (max-width: $desktop-small) {
      width: inherit;
    }
  }

  /*.container h1 {
    @media screen and (max-width: 1160px) {
      font-size: 40px;
    }
    @media screen and (max-width: $desktop-small) {
      font-size: 2em !important;
    }
  }*/

}
/* Since positioning the image, we need to help out the caption */
.carousel-caption {
  z-index: 10;
}

/* Declare heights because of positioning of img element */
.carousel .item {
  background-color: #FFF;
}
.carousel-inner > .item > img {
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
}


.carousel-control {
      width: 10%;
     @media screen and (max-width: $desktop) {
       width: 5%;
     }

}

.carousel-indicators {
    
     @media screen and (max-width: $tablet) {
       display: none;
     }

}

button#toggleCarousel {
    background: transparent;
    border: none;
    color: #FFF;
    font-size: 14px;
}

button#toggleCarouselAria {
  background-color: green;
  bottom: 20px;
  margin-left: 50%;
  padding: 10px 15px;
  position: absolute;
  z-index: 20;
}

a.hero-no-button {
    display: block;
    overflow: hidden;
    width: 100%;
}

a.hero-no-button > img {
    width: 100%;
}

.carousel-inner>.item>a>img {
  width: 100%;
}

/* ROTATOR BUILDER
-------------------------------------------------- */

  /*Center Styles */

  .cntr {
    width: auto !important;
    margin: 5% auto !important;
    display: block;
    text-align: center;
    padding: 0;

    @media screen and (max-width: 1277px) {
      margin-bottom: 2.2% !important;
    }

  }

  .cntr-btn {
    margin: 0 auto !important;
    display: block;
    text-align: center;
    width: max-content;
    width: intrinsic;           /* Safari/WebKit uses a non-standard name */
    width: -moz-max-content;    /* Firefox/Gecko */
    width: -webkit-max-content;
  }

  /* Background color */

  .rt-dark-blue {
    background-color: #182B49 !important;
  }

  .rt-light-blue {
    background-color: $blue !important;
  }

  .rt-neutral-gray {
    background-color: #747678 !important;
  }


  /* Accent color */

  .rt-btn-gold {
    background-color: #C69214 !important;
    color: #000 !important;
  }

  .rt-btn-cyan {
    background-color: #00C6D7 !important;
  }

  .rt-btn-yellow {
    background-color: #FFCD00 !important;
  }

  .rt-btn-orange {
    background-color: #FC8900 !important;
    color: $dark-blue !important;
  }

  /* main text font color */
  .item .rt-text-dark {
    color: $dark-blue;
  }

  .jumbotron-hero .text-indent h1.rt-text-dark {
    text-shadow: 0px 0px 50px rgba(0, 0, 0, 0.25);
  }

  .jumbotron-hero .text-indent p.rt-text-dark {
    text-shadow: 0px 0px 25px rgba(0, 0, 0, 0.25);
  }

  /* Text Box Colors */
  $textBoxColors: dark-opaque, dark-translucent, light-opaque, light-translucent;

  @each $textBoxColor in $textBoxColors {
    .herotextbg-#{$textBoxColor} {
      width: 50%;
      padding: 15px 30px;
      border-radius: 8px;

      @media screen and (max-width: 768px){
        width: 100%;
          }
    }
  }
  
  .herotextbg- {
    &dark-opaque {
      background: #182B49;
    }

    &dark-translucent {
      background: rgba(24, 43, 73, 0.8);
    }

    &light-opaque {
      background: #00629B;
    }

    &light-translucent {
      background: rgba(0, 98, 155, 0.8);
    }
  }

@media (min-width: 768px) {


  .featurette-heading {
    font-size: 50px;
  }
}

@media (min-width: 992px) {
  .featurette-heading {
    margin-top: 120px;
  }
}


.carousel-control.right, .carousel-control.left {
  background-image: none;
}


/* QuickBlock Carousel
-------------------------------------------------- */

.qb-carousel {

    a {
      &:hover .carousel-caption {
        text-decoration: underline;
      }
    }

    .carousel-caption {

      bottom: 55px;
      text-align: left;
      left: 0;
      right: auto;
      margin-left: 20px;
      background:rgba(24, 43, 73, 0.5);
      border-radius: 14px;
      padding: 10px 20px 0 20px;
      text-shadow: none;
      width: auto;
      max-width: 80%;

        h3{
          color: #FFF;
          font-size: 17px;
          line-height: 1.3;
          margin-top: 0;
          margin-bottom: 5px;

          @media(min-width:768px) {
            font-size: 22px;
            font-weight: bold;
            line-height: 1.5;
          }
        }

        p {
          display: none;
          font-size: 15px;

          @media(min-width:768px) {
            display: block;
            margin-bottom: 20px;
            line-height: 1.4;
          }
        }
    }

    .carousel-indicators {
      bottom: 0;
      left: auto;
      list-style: none;
      margin-left: 0;
      margin-right: 10px;
      padding-left: 0;
      right: 0;
      text-align: right;
      width: auto;
    }


}

