}

/** Accessibility enhancements - July 13, 2021 **/

#indicators-container {


  position: absolute;
  bottom: 20px;
  display: block;
  left: 51%;
  transform: translateX(-50%);
  background-color: rgba(0,0,0,0.5);
  border-radius: 12px;
  padding: 0 0 0 10px;
  z-index: 999999;
  
  &.module {
    width: auto;
    padding: 0;
    min-width: 74px;
    left: 0;
    transform: translateX(-60%);
  }
  
  .carousel-indicators {
    /* min-width: 20px; */
    margin: 0;
    padding: 0;
    position: relative;
    left: unset;
    width: auto;
    display: block;
    float: left;
    bottom: 0;
  }

  #toggleCarousel {
    min-width: 20px;
    margin: 0 5px;
    /* padding: 0; */
    position: relative;
    /* left: unset; */
    /* width: 10px; */
    display: block;
    z-index: 9000;
    float: right;
    bottom: -1px;
  }

}

